

































































































































































import Vue from "vue";
import Component from "vue-class-component";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast } from "vant";

import Wintabe from "../../components/Wintabe.vue";
import Winbeet from "../../components/Winbeet.vue";
@Component({
  components: {
    Wintabe,
    Winbeet
    },


  mixins: [mixin]
})
export default class shopIndex extends Vue {
prepareId = ''
shopCartList = []
address = {}
totalPrice = ''
goodsSum = ''
freight =''
getPreInfo(prepareId) {
    Vue.prototype.$reqFormPost1(
      "/prepare/order/query",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        prepareId: prepareId
      },
      res => {
       if (res.returnCode !== 200) {
          this["$Message"].warning(res.message);
          console.log("网络请求错误！");
          return;
        }
      
        this.shopCartList = res.data.shopCartList;
        this.address = res.data.address;
        console.log('address',this.address)
        this.totalPrice = res.data.totalPrice ;
        this.goodsSum = res.data.goodsSum;
        this.freight = res.data.freight;

      }
    );
  }
  pageType = "";
  titlevalue="";
  couponId="";
  titleType="";
  invoiceTitle="";
  invoiceNo="";
  remark="";
 onSubmit() {
    if (!this.address) {
          this["$Message"].warning('请选择一个收货地址');
      return;
    }
    Vue.prototype.$reqFormPost(
      "/prepare/order/submit",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        couponId:this.couponId,
        titleType:this.titleType,
        invoiceTitle:this.invoiceTitle,
        invoiceNo:this.invoiceNo,
        prepareId: this.prepareId,
        remark:this.remark
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }

        this.$router.replace({
          name: "pay",
          query: {
            contactname:this.address['contactname'],
            contactmobile:this.address['contactmobile'],
            address:this.address['province'] + this.address['city'] + this.address['country'] + this.address['address'],
            body:res.data.data.body,
            payId:res.data.data.payId,
            payTotal:res.data.data.payTotal
          }
        });
        console.log(res.data.data);
      }
    );
  }
  mounted() {
    this.prepareId = this.$store.getters[
      Vue.prototype.MutationTreeType.PREPAREID
    ];
    this.getPreInfo(
      this.$store.getters[Vue.prototype.MutationTreeType.PREPAREID]
    );
  }
}



